.App {
  max-width: fit-content;
  margin-left: auto;
  margin-right: auto;
}
.Leaderboard {
  padding: 10px;
}
.Leaderboard__table {
  width: 250px;
    border: 1px solid;
    padding: 10px;
  
  td {
    padding: 5px;
  }
}
.Game {
  padding: 10px;
  text-align: center;
}
.Game__table {
    width: 250px;
    border: 1px solid;
    padding: 10px;
  
  td {
    padding: 5px;
  }
}
.Game__win {
  background-color: green;
}
.Game__lose{
  background-color: red;
}

